.section_avatar {
  width: 30%;
  height: 100vh;
  background-color: #2b2c2c;
  display: flex;
  align-items: center;
  padding-left: 5%;
  justify-content: center;
}
.avatar {
  display: flex;
  justify-content: center;
  &_img {
    width: 80%;
    height: 80%;
    border-radius: 50%;
  }
}

@media screen and (max-width: 1199px) {
  .padding_left {
    padding-left: 5%;
  }
  .padding_right {
    padding-right: 5%;
  }
}

/* Portrait tablets and small desktops */
@media screen and (max-width: 991px) {
  .section_avatar {
   display: none;

  }
  .padding_left {
    padding-left: 5%;
  }
  .padding_right {
    padding-right: 5%;
  }
}

/* Landscape phones and portrait tablets */
