
.skills {
  display: flex;
  flex-direction: row;

}
.skills_title {
  background-color: #282929;
  color: #a4a5a6;
  width: 30%;
  display: flex;
  flex-direction: column;
  text-align: right;
  padding: 2rem;
}
h3 {
  text-transform: uppercase;
}
a {
  text-decoration: none;
  color: #979899;
}
.skills_text {
  background: #2a2c2c;
  color: #979899;
  padding: 15%;
  width: 70%;
  font-size: 1.2rem;
  display: flex;
  flex-flow: row wrap;
  align-content: space-between;
  justify-content: space-between;
}
.element {
  margin-bottom: 1%;
  width: 45%;
  &_text {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
  }
}
.element p {
  margin-left: 1%;
  text-transform: uppercase;
}
.percent {
  color: #e0a80d;
}
.skills_text {
  background-color: #2a2c2c;
  color: #979899;
  padding: 2rem;
  width: 70%;
}
/* Portrait tablets and medium desktops */
@media screen and (max-width: 1199px) {

}

/* Portrait tablets and small desktops */
@media screen and (max-width: 991px) {
  .skills_title {
    width: 20%;
  }
  .skills_text {
    width: 80%;
  }
}

/* Landscape phones and portrait tablets */
@media screen and (max-width: 767px) {
  .skills {
    display: flex;
    flex-direction: column;
  }
  .skills_title {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
  }
  .skills_text {
    width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    align-items: center;
  }
  .element {
    width: 90%;
  }
}