@import "maininfo";
@import "avatar";
@import url('https://fonts.googleapis.com/css?family=Lato');
@import "intro";
@import "skills";
@import "experience";
@import "projects";
@import "contact";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-size: 1rem;
  font-family: Lato, "Helvetica Neue", Helvetica, Arial, sans-serif;
  line-height: 1.75;
  height: 100%;
}
.container {
  width: 100%;
  margin: 0 auto;
  background-color: #404242;
  height: 100vh;
}
.padding_left {
  padding-left: 10%;
}
.padding_right {
  padding-right: 10%;
}
.main {
  display: flex;
  flex-direction: row;
}


/* Portrait tablets and medium desktops */
@media screen and (max-width: 1199px) {
  .padding_left {
    padding-left: 5%;
  }
  .padding_right {
    padding-right: 5%;
  }
}

/* Portrait tablets and small desktops */
@media screen and (max-width: 991px) {
  .main {
    display: flex;
    flex-direction: column;
  }
  .padding_left {
    padding-left: 5%;
  }
  .padding_right {
    padding-right: 5%;
  }
}

/* Landscape phones and portrait tablets */
@media screen and (max-width: 767px) {
  .padding_left {
    padding-left: 2%;
  }
  .padding_right {
    padding-right: 2%;
  }
}





