$color-grey: #a4a5a6;
$color-yellow: #e0a80d;
$color-dark-grey: #404242;

a {
  padding: 2%;
  border-radius: 10%;
  text-align: center;
  width: 50px;
  height: 50px;
}
hr {
  height: 2px;
  background: $color-dark-grey;
  font-size: 0;
  border: 0;

}

a i {
  font-size: 40px;
  color: #3f4040;
}
a:hover {
  color: #3f4040;;
  transition: 1s;
}
a:hover i {
  color: #e0a80d;
  transition: 1s;
}
.section_info {
  width: 70%;
  background-color: #2d2e2e;
  padding: 5%;
  height: 100vh;
  display: flex;
  flex-direction: column;

}
.header_top {
  height: 100%;
  display: flex;
  justify-content: space-between;

}
.header_bottom {
  height: 100%;
}
.main_info {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  &_name {
    text-transform: lowercase;
    color: $color-grey;
    font-weight: 300;
    font-size: 3rem;
    padding: 0;
    transform: translateY(45%);
  }
  &_surname {
    text-transform: uppercase;
    margin-bottom: 2%;
    color: $color-yellow;
    font-weight: 700;
    font-size: 3.5rem;
  }
  &_profession {
    color: $color-grey;
    font-size: 1.5rem;
    font-weight: 300;
  }
}
.social_networking {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
}
.header_line {
  margin-bottom: 1.75rem;
  height: 1px;
  margin-top: 1%;
  color: #404242;
}
.contacts {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: $color-grey;
}
@media screen and (min-width: 1200px) {

}

/* Portrait tablets and medium desktops */
@media screen and (max-width: 1199px) {



}

/* Portrait tablets and small desktops */
@media screen and (max-width: 991px) {

  .section_info {
    width: 100%;
  }
  .main_info {
    display: flex;
    justify-content: space-between;
  }
  .social_networking {
    display: flex;
    justify-content: flex-start;
  }
}

/* Landscape phones and portrait tablets */
@media screen and (max-width: 767px) {
  .main_info {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    align-items: center;
  }
  .contacts {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    align-items: center;
  }
  .location {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    align-items: center;
  }
  .phone {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    align-items: center;
  }
  .web {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    align-items: center;
  }
  .email {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    align-items: center;
  }
}
