.projects {
  display: flex;
  flex-direction: row;
  &_title {
    background-color: #2c2e2e;
    color: #a4a5a6;
    width: 30%;
    display: flex;
    flex-direction: column;
    text-align: right;
    padding: 2rem

  }
  &_text {
    background-color: #272828;
    width: 70%;
    color: #979899;
    padding: 1.75rem;

  }
  &_container {
    display: flex;
    flex-direction: column;
  }
}

.project {
  display: flex;
  justify-content: space-between;
  &_description {
    width: 70%;
    display: flex;
    flex-direction: column;
  }
 &_link {
   width: 30%;
   display: flex;
   align-items: center;
   justify-content: center;
  justify-items: center;
 }
  &_line {
    background-color: #e0a80d;
    height: 5px;
    width: 30%;
    margin-top: 0.55rem;
    margin-bottom: 0.935rem;
    text-align: left;

  }
}
h3 {
  color: #e0a80d;
}
/* Portrait tablets and medium desktops */
@media screen and (max-width: 1199px) {

}

/* Portrait tablets and small desktops */
@media screen and (max-width: 991px) {
  .projects_title {
    width: 20%;
  }
  .projects_text {
    width: 80%;
  }
}

/* Landscape phones and portrait tablets */
@media screen and (max-width: 767px) {
  .projects {
    display: flex;
    flex-direction: column;
  }
  .projects_title {
display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
  }
  .projects_text {
    width: 100%;
  }
}

