
.experience {
  display: flex;
  flex-direction: row;

  .experience_title {
    background-color: #2a2c2c;
    color: #a4a5a6;
    width: 30%;
    display: flex;
    flex-direction: column;
    text-align: right;
    padding: 2rem

  }
  .experience_text {
    padding-top: 1.75rem;
    padding-left: 1.75rem;
    padding-bottom: 1.75rem;

    color: #979899;
    background-color: #2d2e2e;
    width: 70%;
  }
}
h3, h2{
  text-transform: uppercase;
}


.c-timeline {
  border-left-width: 2px;
  border-left-style: solid;
  margin-left: calc(1rem - 2px);
  padding-left: 25px;
  padding-bottom: 1.75rem;

  position: relative;
}
.t-border-color {
  border-color: #404242;
}
.c-timeline__item {
  position: relative;
}
.t-timeline__point {
  box-shadow: 0 0 0 6px #2d2e2e;
}
.t-primary-bg, .t-primary-color-line {
  background: #e0a80d;
}
.c-timeline__point {
  height: 10px;
  width: 10px;
  position: absolute;
  left: -31px;
  top: 3px;
  border-radius: 50%;
}
.content {
  display: flex;
  justify-content: space-evenly;
  box-sizing: inherit;
}
.c-work__timeframe, .c-work__location {
  opacity: .6;
  font-size: 0.7rem;
}

.main_part {
  width: 30%;
}
.work_info {
  width: 70%;
}
text {
  font-size: 14px
}
.c-timeline__end {
  height: 1px;
  width: 16px;
  position: absolute;
  left: -9px;
  bottom: 0;
  border-width: 1px;
  border-style: solid;
}
.t-border-color {
  border-color: #404242;
}

/* Portrait tablets and medium desktops */
@media screen and (max-width: 1199px) {

}

/* Portrait tablets and small desktops */
@media screen and (max-width: 991px) {
  .experience {
    display: flex;
    flex-direction: row;

    .experience_title {
      background-color: #2a2c2c;
      color: #a4a5a6;
      width: 20%;
      display: flex;
      flex-direction: column;
      text-align: right;
      padding: 2rem

    }
    .experience_text {
      padding-top: 1.75rem;
      padding-left: 1.75rem;
      padding-bottom: 1.75rem;

      color: #979899;
      background-color: #2d2e2e;
      width: 80%;
    }
  }
  h3, h2{
    text-transform: uppercase;
  }
}

/* Landscape phones and portrait tablets */
@media screen and (max-width: 767px) {
  .experience {
    display: flex;
    flex-direction: column;

    .experience_title {

      width: 100%;
      display: flex;
      flex-direction: column;
align-items: center    }
    .experience_text {
      padding-top: 1.75rem;
      padding-left: 1.75rem;
      padding-bottom: 1.75rem;

      color: #979899;
      background-color: #2d2e2e;
      width: 100%;
    }
  }
  h3, h2{
    text-transform: uppercase;
  }

}