.intro {
  display: flex;
  flex-direction: row;

}
.intro_title {
  background-color: #292a2a;
  color: #a4a5a6;
  width: 30%;
  display: flex;
  flex-direction: column;
  text-align: right;
  padding: 2rem
}
h3 {
  text-transform: uppercase;
}
a {
  text-decoration: none;
  color: #979899;
}
.intro_text {
  background: #2c2e2e;
  color: #979899;
  padding: 2rem;
  width: 70%;
}

/* Portrait tablets and medium desktops */
@media screen and (max-width: 1199px) {

}

/* Portrait tablets and small desktops */
@media screen and (max-width: 991px) {
  .intro_title {
    width: 20%;
  }
  .intro_text {
    width: 80%;
  }
}

/* Landscape phones and portrait tablets */
@media screen and (max-width: 767px) {
  .intro {
    display: flex;
    flex-direction: column;
  }
  .intro_title {
    display: flex;
    align-items: center;
    width: 100%;
  }
  .intro_text {
    width: 100%;
  }
}
